<template>
  <div class="container">
    <div id="gallery" class="flex items-start">
      <div id="col1">
        <GalleryItem
          class="gallery-item"
          v-for="photo in photos1"
          :key="photo.id"
          :photo="photo"
        />
      </div>
      <div id="col2">
        <GalleryItem
          class="gallery-item"
          v-for="photo in photos2"
          :key="photo.id"
          :photo="photo"
        />
      </div>
      <div id="col3">
        <GalleryItem
          class="gallery-item"
          v-for="photo in photos3"
          :key="photo.id"
          :photo="photo"
        />
      </div>
    </div>
    <div v-if="more" class="text-center my-10">
      <Preloader v-if="updating" />
      <div
        v-else
        @click="$emit('loadMore')"
        class="btn-large waves-effect waves-light grey darken-4"
      >
        Load more <i class="material-icons right">arrow_forward</i>
      </div>
    </div>
  </div>
</template>

<script>
import GalleryItem from './GalleryItem'
import Preloader from './Preloader'

export default {
  name: 'Gallery',

  props: {
    photos: Array,
    more: Boolean,
    updating: Boolean,
  },

  components: {
    GalleryItem,
    Preloader,
  },

  data() {
    return {
      photos1: [],
      photos2: [],
      photos3: [],
      height1: 0,
      height2: 0,
      height3: 0,
      cols: null,
    }
  },

  methods: {
    resizeHandler() {
      const w = window.innerWidth
      const prevCols = this.cols
      if (w < 600) {
        this.cols = 1
      } else if (w < 1100) {
        this.cols = 2
      } else {
        this.cols = 3
      }

      if (prevCols && this.cols !== prevCols) {
        this.photos1 = []
        this.photos2 = []
        this.photos3 = []
        this.height1 = 0
        this.height2 = 0
        this.height3 = 0
        this.resolveCols(this.photos)
      }
    },

    resolveCols(newItems) {
      newItems.forEach((photo) => {
        if (this.cols === 2) {
          if (this.height2 < this.height1) {
            this.photos2.push(photo)
            this.height2 += photo.height / photo.width
            return
          }
        }

        if (this.cols === 3) {
          if (this.height3 < this.height2 && this.height2 < this.height1) {
            this.photos3.push(photo)
            this.height3 += photo.height / photo.width
            return
          }

          if (this.height2 < this.height1) {
            this.photos2.push(photo)
            this.height2 += photo.height / photo.width
            return
          }
        }

        this.photos1.push(photo)
        this.height1 += photo.height / photo.width
        return
      })
    },
  },

  watch: {
    photos: function(newPhotos, oldPhotos) {
      if (newPhotos[0].id === oldPhotos[0].id) {
        const diff = newPhotos
          .map((p) => p.id)
          .filter((id) => !oldPhotos.find((p) => p.id === id))
          .map((id) => {
            return newPhotos.find((p) => p.id === id)
          })
        // console.log(diff)

        this.resolveCols(diff)

        return
      }

      this.photos1 = []
      this.photos2 = []
      this.photos3 = []
      this.height1 = 0
      this.height2 = 0
      this.height3 = 0
      this.resolveCols(newPhotos)
    },
  },

  destroyed() {
    window.removeEventListener('resize', this.resizeHandler)
  },

  created() {
    window.addEventListener('resize', this.resizeHandler)

    this.resizeHandler()
    this.resolveCols(this.photos)
  },
}
</script>

<style lang="scss"></style>
